import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './footer.css';

class Footer extends Component {

    render() {

        const year = new Date().getUTCFullYear();

        return (
            <footer className='mt-2 shadow-lg'>
                <div className='container py-5'>
                <div className='row py-4'>
                    <div className='col-lg-4 col-md-6 mb-4 mb-lg-0'><img src='img/logo.png' alt='' width='180' className='mb-3' />
                    <p className='font-italic'> MedEstet.       .</p>
                    <ul className='list-inline mt-4'>
                        
                        <li className='list-inline-item'>
                            <a href='https://www.facebook.com/medestetpro' target='_blank' rel='noreferrer' title='Facebook'>
                                <i className='fab fa-facebook'></i>
                            </a>
                        </li>

                        <li className='list-inline-item'>
                            <a href='https://www.instagram.com/medestet_pro' target='_blank' rel='noreferrer' title='Instagram'>
                                <i className='fab fa-instagram'></i>
                            </a>
                        </li>

                        <li className='list-inline-item'>
                            <a href='https://www.youtube.com/channel/UCYVT56f9R5c8P6ScUtnNXRA?view_as=subscriber' target='_blank' rel='noreferrer' title='Youtube'>
                                <i className='fab fa-youtube'></i>
                            </a>
                        </li>

                        <li className='list-inline-item'>
                            <a href='https://t.me/Medestetprobot' target='_blank' rel='noreferrer' title='Telegram'>
                                <i className='fab fa-telegram-plane'></i>
                            </a>
                        </li>

                    </ul>
                    </div>
                    <div className='col-lg-2 col-md-6 mb-4 mb-lg-0'>
                    <h6 className='text-uppercase font-weight-bold mb-4'></h6>
                    <ul className='list-unstyled mb-0'>
                        <li className='mb-2'><Link to='/courses/'></Link></li>
                        <li className='mb-2'><Link to='/workshops/'></Link></li>
                        <li className='mb-2'><Link to='/webinars/'></Link></li>
                    </ul>
                    </div>
                    <div className='col-lg-2 col-md-6 mb-4 mb-lg-0'>
                    <h6 className='text-uppercase font-weight-bold mb-4'></h6>
                    <ul className='list-unstyled mb-0'>
                        <li className='mb-2'><Link to='/shop/'></Link></li>
                        <li className='mb-2'><Link to='/video/'></Link></li>
                        <li className='mb-2'><Link to='/news/'></Link></li>
                        <li className='mb-2'><Link to='/blog/'></Link></li>
                    </ul>
                    </div>
                    <div className='col-lg-4 col-md-6 mb-lg-0'>
                    <h6 className='text-uppercase font-weight-bold mb-4'></h6>
                    <p className='text-muted mb-4'>   ,           .</p>
                    <div className='p-1 rounded border'>
                        <div className='input-group'>
                        <input type='email' placeholder='  email' aria-describedby='button-addon1' className='form-control border-0 shadow-0' />
                        <div className='input-group-append'>
                            <button id='button-addon1' type='submit' className='btn btn-link'><i className='fa fa-paper-plane'></i></button>
                        </div>
                        </div>
                    </div>
                    </div>
                    </div>
                </div>

                <div className='py-4'>
                    <div className='container text-center'>
                        <p className='text-muted mb-0 py-2'> { year } Medestet Pro</p>
                    </div>
                </div>
            </footer>
        );
    };
};

export default Footer;